@import 'src/Assets/Styles/Helpers/Helpers.Style.scss';
.LocationButtonComponent-wrapper {
  position: relative;
  .line
  {
    border-top: 1px solid #c7c7cc;
    padding-bottom: 16px;
    position: absolute;
    left: 0;
    bottom: 60px;
  }
  .Button-root {
    .MuiButtonBase-root {
      font-weight: 900;
      background-color: $c-Basic;
      color: $c-primary;
      min-width: 7rem;
      min-height: 34px;
      border-radius: 7px;
    }
  }
}
.Location-menu-collapse-wrapper {
  border-radius: 0.2rem;
  background-color: $c-white;
  box-shadow: $bs-secondary;
  padding: 0 1rem;
  min-width: 7.5rem;
  -webkit-border-radius: 0.2rem;
  -moz-border-radius: 0.2rem;
  -ms-border-radius: 0.2rem;
  -o-border-radius: 0.2rem;
}
.Location-menu-emp {
  right: unset !important;
  .collapse-wrapper.is-absolute {
    position: absolute;
    right: unset !important;
    z-index: 3;
    background-color: #e2e2e2;
    min-width: 147px;
    height: 38px;
  }
}
.LocationButtonComponent-auto-wraper {
  min-width: 325px;
  padding: 30px 30px 20px;
  width: 413px;
  background-color: white;
  border-radius: 10px;
  min-height: 13rem;
  .bbt-wrpaer-filter
  {
    display: inline-flex;
    padding-top: 3rem;
  }
}

.Location-inputs-wraper {
  display: inline-flex;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 1.5rem;
  .autocomplete-wrapper {
    min-width: auto;
  }
  .county-title {
    display: flex;
    justify-content: center;
    align-items: center;
  }

}
